/* Base styles */
.Navbar {
    position: sticky;
    background-color: white;
    height: 90px;
    border-bottom: 2px rgb(235, 235, 235) solid;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 20px;
    z-index: 1000;
}

.navbarleft {
    display: flex;
    align-items: center;
    margin-left: 10px;
}

.navbarName {
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    color: rgb(20, 83, 165);
}

.navbarlinks {
    display: flex;
    gap: 15px;
    align-items: center;
}

.buttonpanel {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.navbarbuttons {
    background: none;
    border: none;
    cursor: pointer;
    font-size: 15px;
    color: rgb(15, 44, 82);
    transition: transform 0.1s;
}

.navbarbuttons:hover {
    transform: scale(1.1);
}

.circle {
    border-radius: 50%;
    height: 5px;
    width: 5px;
    background-color: #08203d;
    transition: opacity 0.2s;
    opacity: 0;
}

.navbarnext {
    display: flex;
    gap: 10px;
    margin-right: 20px;
}

.navbarIcons {
    cursor: pointer;
    color: #124686;
}

.navbarMenuIcon {
    display: none;
    cursor: pointer;
    color: #124686;
}

.navbarPopup {
    position: absolute;
    top: 90px;
    right: 20px;
    background-color: white;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    padding: 10px;
    display: flex;
    flex-direction: column;
    gap: 10px;
    z-index: 1001;
    transition: 1s ease;
}

.navbarPopupButtons {
    background: none;
    border: none;
    cursor: pointer;
    font-size: 15px;
    color: rgb(15, 44, 82);
    text-align: left;
}

@media (max-width: 768px) {

    .navbarleft,
    .navbarlinks {
        display: none;
    }

    .navbarnext {
        display: flex;
        order: -1;
        /* Move to the start */
        margin-right: auto;
        /* Align to the left */
    }

    .navbarMenuIcon {
        display: block;
    }
}

