/*-----------------------------Values-------------------------------*/
.ValuesContainer {
    height: 700px;
    display: grid;
    grid-template-rows: 150px auto;
    border: solid rgb(237, 243, 253);
    border-width: 2px;
    padding: 20px;
    overflow: hidden;
    background-color: #f2f5f8;
}

.Valuesline {
    color: #08203d;
    height: 1px;
}

.ValuesUpperContainer {
    display: grid;
    grid-template-rows: 0.5fr 0.1fr;
    margin-top: 25px;
    font-size: 30px;
    font-weight: 400;
    margin-left: auto;
    margin-right: auto;
    font-family: 'Impact';
    color: rgb(228, 242, 253);
    letter-spacing: 10px;
}

.ValuesOUR {
    color: #1c5eaf;
}

.ValuesText {
    font-size: 40px;
    margin-top: -100px;
    margin-bottom: 100px;
    letter-spacing: 5px;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    color: rgb(38, 83, 143);
    transition: 2s ease-out;
    text-align: center;
}

.ValuesBottomContainer {
    display: flex;
    align-items: center;
    justify-content: center;
}

.slide-in {
    animation: slideIn 1s forwards;
}

.slide-out {
    animation: slideOut 1s forwards;
}

@keyframes slideIn {
    from {
        transform: translateX(-100%);
        opacity: 0;
    }

    to {
        transform: translateX(0);
    }
}

@keyframes slideOut {
    from {
        transform: translateX(0);
    }

    to {
        transform: translateX(100%);
        opacity: 0;
    }
}

@media (max-width: 780px) {
    .ValuesContainer {
        height: 500px;
    }

    .ValuesUpperContainer {
        font-size: 18px;
    }

    .ValuesText {
        font-size: 25px;
    }
}

@media (max-width: 480px) {
    .ValuesText {
        font-size: 24px;
        letter-spacing: 1px;
        color: #0b3b77;
    }

    .ValuesUpperContainer {
        letter-spacing: 2px;
        font-weight: 600;
    }
}

/*---------------------------------------Footer----------------------------*/
.footer {
    background: #152F4F;
    color: white;

    .links {
        ul {
            list-style-type: none;
        }

        li a {
            color: white;
            transition: color .2s;

            &:hover {
                text-decoration: none;
                color: #4180CB;
            }
        }
    }


    .about-company {
